<template>
	<div>
		<!-- 个人信息 -->
		<div class="userInfo-container">
			<div class="box"></div>
			<div class="userInfo-item">
				<div class="userInfo-item-left">
					<!-- 头像 -->
					<div>
						<el-avatar :size="75" :src="user.headSculpture" @click.native="updateAvatarDialogVisible=true" style="cursor: pointer;"></el-avatar>
					</div>
					<div>
						<p>{{user.username}}</p>
						<span>
							简介: {{user.personalizedSignature}}
							<i class="el-icon-edit" @click="updateDialogVisible=true" style="cursor: pointer;"></i>
						</span>
						<span>Email: {{user.email}}</span>
					</div>
				</div>
				<div class="fans-info">
					<div>
						<p>{{fans}}</p>
						<span>粉丝数</span>
					</div>
					<div>
						<p>{{FollowNum}}</p>
						<span>关 注</span>
					</div>
				</div>
			</div>
		</div>
		
		<!-- 菜单导航 -->
		<el-menu :default-active="'1'" mode="horizontal" @select="handleSelect">
			<el-menu-item index="1">我的关注</el-menu-item>
			<el-menu-item index="2">我的粉丝</el-menu-item>
			<el-menu-item index="3">我的评论</el-menu-item>
			<el-menu-item index="4">我的帖子</el-menu-item>
		</el-menu>
		<!-- 我的关注 -->
		<div class="follow-container" v-if="selectIndex == 1">
			<div class="follow-item" v-for="item in followUserList" @click="routeToOthersPage(item)">
				<div class="follow-content">
					<div>
						<el-avatar :size="70" :src="item.headSculpture"/>
					</div>
					<div class="follow-info">
						<p>{{item.username}}</p>
						<span v-if="user.personalizedSignature">简介: {{user.personalizedSignature}}</span>
						<span v-else>简介: 这个人什么也没写</span>
						<span>最近更新：{{item.updateTime}}</span>	
					</div>
				</div>
				<el-button @click.stop="handleNotFollow(item.userId)">取消</el-button>
			</div>
			<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
						layout="prev, pager, next" @current-change="handleCurrentChange">
				</el-pagination>
			</div>
		</div>
		
		<!-- 我的粉丝 -->
		<div class="follow-container" v-if="selectIndex == 2">
			<div class="follow-item" v-for="item in myFansList" @click="routeToOthersPage(item)">
				<div class="follow-content">
					<div>
						<el-avatar :size="70" :src="item.headSculpture"/>
					</div>
					<div class="follow-info">
						<p>{{item.username}}</p>
						<span v-if="user.personalizedSignature">简介: {{user.personalizedSignature}}</span>
						<span v-else>简介: 这个人什么也没写</span>
						<span>最近更新：{{item.updateTime}}</span>	
					</div>
				</div>
				<el-button type="primary" @click.stop="handleFollow(item.userId)">关注</el-button>
			</div>
			<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="myFansListPage.pageNum" :page-size="myFansListPage.pageSize" :total="myFansListPage.total"
						layout="prev, pager, next" @current-change="myFansHandleCurrentChange">
				</el-pagination>
			</div>
		</div>
		
		<!-- 我的评论 -->
		<div class="comment-container" v-if="selectIndex == 3">
			<my-comment-list :comment="item" v-for="item in commentList"></my-comment-list>
		<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="commentQueryInfo.pageNum" :page-size="commentQueryInfo.pageSize" :total="commentQueryInfo.total"
						layout="prev, pager, next" @current-change="commentHandleCurrentChange">
				</el-pagination>
			</div>
		</div>
		
		<!-- 我的帖子 -->
		<div class="comment-container" v-if="selectIndex == 4">
			<my-comment-list :comment="item" v-for="item in myPostList"></my-comment-list>
		<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="myPostQueryInfo.pageNum" :page-size="myPostQueryInfo.pageSize" :total="myPostQueryInfo.total"
						layout="prev, pager, next" @current-change="myPostHandleCurrentChange">
				</el-pagination>
			</div>
		</div>
		

		<!-- 修改头像对话框 -->
		<el-dialog title="修改头像" :visible.sync="updateAvatarDialogVisible" width="60%">
			<el-upload action="*" ref="upload" list-type="picture-card"
				:auto-upload="false"
				:http-request="handleUpdateAvatar"
				:limit="1">
				<img v-if="imageUrl" :src="imageUrl">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span slot="footer">
				<el-button @click="updateAvatarDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="upload">修 改</el-button>
			</span>
		</el-dialog>
		
		<!-- 修改签名对话框 -->
		<el-dialog title="修改简介" :visible.sync="updateDialogVisible" width="40%" @close="updateDialogClosed">
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="简介" prop="sign">
						<el-input v-model="updateForm.sign" maxlength="100"></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="updateDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="updateSign">修 改</el-button>
			</span>
		</el-dialog>
		
	</div>
</template>

<script>
	import commentListComponent from './user_component/myCommentComponent.vue'
	import { follow,getMyCommentList,getFollowList,getUser,getFollowNum,getFans,getFansList,updateAvatar,notFollow,getPostListByUserId } from '../../api/user'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default{
		name: 'userCenter',
		inject: ['reload'],
		data(){
			return{
				//用户信息
				user: [],
				followUserList: [],		//关注列表
				commentList: [],		//评论列表
				myPostList: [],			//我的帖子
				myFansList: [],			//我的粉丝
				//修改头像的头像地址
				imageUrl: '',
				fans: 0,
				FollowNum: 0,
				//选中的菜单项
				selectIndex: 1,
				//分页
				queryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				commentQueryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				myPostQueryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				myFansListPage: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				//修改信息对话框显示&隐藏
				updateDialogVisible: false,
				//修改头像对话框显示&隐藏
				updateAvatarDialogVisible: false,
				//修改简介的表单数据
				updateForm: {
					sign: ''
				},
				//修改简介表单的验证规则
				updateFormRules: {
					sign: [
						{ required:true, message:'请输入内容', trigger:'blur'}
					],
				}
			}
		},
		methods:{
			
			/**
			 * 获取关注列表数据
			 */
			handleGetFollowList(){
				const data = {
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getFollowList(data).then(res => {
					if(responseStatus(res)){
						this.queryInfo = res.data.data;
						this.followUserList = res.data.data.list; 
					}
				})
			},
			/**
			 * 获取评论列表数据
			 */
			handleGetMyCommentList(){
				const data = {
					pageNum: this.commentQueryInfo.pageNum,
					pageSize: this.commentQueryInfo.pageSize
				}
				getMyCommentList(data).then(res => {
					if(responseStatus(res)){
						this.commentQueryInfo = res.data.data;
						this.commentList = res.data.data.list; 
					}
							   	
				})
			},
			/**
			 * 获取我的帖子
			 */
			handleGetMyPostList(){
				const data = {
					pageNum: this.myPostQueryInfo.pageNum,
					pageSize: this.myPostQueryInfo.pageSize
				}
				getPostListByUserId(data).then(res => {
					if(responseStatus(res)){
						this.myPostQueryInfo = res.data.data;
						this.myPostList = res.data.data.list; 
					}
				})
			},
			
			/* 获取我的粉丝 */
			handleGetMyFansList(){
				const data = {
					pageNum: this.myFansListPage.pageNum,
					pageSize: this.myFansListPage.pageSize
				}
				getFansList(data).then(res => {
					if(responseStatus(res)){
						this.myFansListPage = res.data.data;
						this.myFansList = res.data.data.list;
					}
				})
			},
			/**
			 * 从网络获取用户信息
			 */
			handleGetUser(){
				getUser().then(res => {
					if(responseStatus(res)){
						this.user = res.data.data; 
					}
				})
			},
			
			/**
			 * 获得关注数
			 */
			handleGetFollowNum(){
				getFollowNum().then(res => {
					if(responseStatus(res))
						this.FollowNum = res.data.data; 
				})  	
			},
			
			/**
			 * 获取粉丝数
			 */
			handleGetFans(){
				getFans().then(res => {
					if(responseStatus(res))
						this.fans = res.data.data; 
				})
			},
			
			
			

			
			//////事务处理函数///////
			
			 //上传头像
			handleUpdateAvatar(file){
				const formData = new FormData();
				formData.append('file', file.file);
				updateAvatar(formData).then(res => {
					if(responseStatus(res)){
				    	this.updateAvatarDialogVisible = false;
				    	this.$message.success("修改成功!!!");
						this.handleGetUser();
						this.$bus.$emit('change');
					}		   	
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			//修改简介
			updateSign(){
				this.$axios.post('/user/update',this.$qs.stringify({
					personalizedSignature: this.updateForm.sign }))
						   .then(res => {
								if(res.status !== 200)
								   	return this.$message.error("修改签名失败!!!");
								else if(res.data.status !== 10000)
								   	this.$message.error(res.data.msg+"!!!");
								else{
									this.updateDialogVisible = false;
									this.$message.success("修改成功!!!");
									this.hanldeGetUser();
								}		   	
							})
			},
			/**
			 * 取消关注/取消关注
			 *
			 * @param {Object} userId
			 */
			handleNotFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				notFollow(data).then((res) => {
					 if(responseStatus(res)){
						this.handleGetFollowList();
						this.$message.success("取消成功");			   
					}					
				});
			},
			
			/* 关注用户 */
			handleFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				if(data.beSubscribe == null){
					data.beSubscribe = this.user.userId
				}
				follow(data).then(res => {
					if(responseStatus(res)){
						this.handleGetFollowList();
						return this.$message.success("关注成功!");
					}
				});
			},
			
			
			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(item){
				let othersInfo = JSON.stringify(item);
				window.sessionStorage.setItem('othersInfo',othersInfo);
				this.$router.push('/othersInfo');
			},
			
			handleSelect(index){
				this.selectIndex = index;
			},
			/* 当前页数改变 */
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.handleGetFollowList();
			},
			commentHandleCurrentChange(newPage){
				this.commentQueryInfo.pageNum = newPage;
				this.handleGetMyCommentList();
			},
			myPostHandleCurrentChange(newPage){
				this.myPostQueryInfo.pageNum = newPage;
				this.handleGetMyPostList();
			},
			myFansHandleCurrentChange(newPage){
				this.myFansListPage.pageNum = newPage;
				this.handleGetMyFansList();
			},
			//监听修改信息对话框关闭事件
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //清空输入框
			}
		},
		components:{
			myCommentList: commentListComponent
		},
		created() {
			this.handleGetUser();
			this.handleGetFans();
			this.handleGetFollowNum();
			this.handleGetFollowList();
			this.handleGetMyCommentList();
			this.handleGetMyPostList();
			this.handleGetMyFansList();
		}
	}
</script>

<style scoped="scoped">
	/**
	 * 个人信息
	 */
	.userInfo-container{
		height: 300px;
		position: relative;
		font-size: 14px;
		background: url(../../assets/background/background.jpg) no-repeat;
		background-size: 100% 100%;
		border-radius: 10px;
	}
	.box {
		padding-top: 200px;
	}
	.userInfo-item{
		max-width: 1200px;
		display: flex;
		margin: 0 20px;
		justify-content: space-between;
	}
	.userInfo-item-left{
		display: flex;
		align-items: center;
	}
	.userInfo-item-left span{
		margin: 5px 0;
		display: block;
	}
	.userInfo-item-left div:nth-child(2){
		margin-left: 10px;
	}
	.userInfo-item-left div:nth-child(2) p{
		margin: 10px 0;
		font-size: 18px;
	}
	.fans-info{
		display: flex;
		align-items: center;
		text-align: center;
	}
	.fans-info div{
		margin-right: 30px;
	}
	
	.fans-info p{
		margin-bottom: 10px;
	}
	
	/**
	 * 我的关注
	 */
	.follow-container {
		background-color: #F8F8F8;
	}
	.follow-menu {
		padding: 0 20px;
	}
	.follow-item {
		display: flex;
		align-items: center;
		padding: 20px;
		color: #282C34;
		cursor: pointer;
		justify-content: space-between;
	}
	.follow-item p:hover {
		color: #409EFF;
	}
	.follow-content{
		display: flex;
	}
	.follow-info {
		padding-left: 15px;
	}
	.follow-info p{
		margin: 5px 0;
		font-size: 18px;
	}
	.follow-info span{
		display: block;
		color: #666666;
		margin-top: 10px;
		font-size: 14px;
	}
	.follow-pagination {
		margin-top: 10px;
		text-align: center;
		background-color: #FFFFFF;
	}
	
</style>
