<template>
	<div>
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" style="margin-bottom: 10px;">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>文章管理</el-breadcrumb-item>
		  <el-breadcrumb-item>回收站</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card>
			<div>
				<!-- 分类列表 -->
				<el-table :data="trashList" style="width: 100%">
					<el-table-column prop="postId" label="文章ID"/>
					<el-table-column prop="title" label="文章主题"/>
					<el-table-column prop="categoryName" label="分类"/>
					<el-table-column prop="userName" label="用户名"/>
					<el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="恢复" placement="top" :enterable="false">
								<el-button type="success" size="mini" icon="el-icon-refresh" @click="handleRollBackPost(scope.row.postId)"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!-- 分页区域 -->
			<el-pagination :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
					layout="prev, pager, next" @current-change="handleCurrentChange">
			</el-pagination>
		</el-card>
	</div>
</template>

<script>
	import { getTrashList,rollBackPost } from '../../api/admin'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'trashStation',
		data(){
			return {
				trashList:[],
				//分页信息
				queryInfo: {
					pageNum: 1,		
					pageSize: 10,	
					total: 0		
				},
			}
		},
		methods:{
			/**
			 * 获取回收站列表
			 */
			handleGetTrashList(){
				const data = {
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getTrashList(data).then((res) => {
					if(responseStatus(res)){
						
						this.queryInfo = res.data.data;
						this.trashList = res.data.data.list;
					}
						
				})
			},
			handleRollBackPost(postId){
				const data = [];
				data.push(parseInt(postId));
				rollBackPost(data).then(res => {
					if(responseStatus(res)){
						this.handleGetTrashList();
						return this.$message.success("恢复成功");
						
					}
				})
				
			},
			/**
			 * 分页处理
			 * @param {Object} newPage
			 */
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.handleGetTrashList();
			},
		},
		created() {
			this.handleGetTrashList();
		}
	}
</script>

