<template>
	<div class="myself-container">
		<!-- 个人信息 -->
		<div class="userInfo-background">
			<div class="box"></div>
			<div class="user-detail-item">
				<div class="user-info">
					<div>
						<el-avatar :size="70" :src="user.headSculpture"></el-avatar>
					</div>
					<div>
						<p>{{user.username?user.username:user.userName}}</p>
						<span v-if="user.personalizedSignature">简介: {{user.personalizedSignature}}</span>
						<span v-else>简介: 这个人什么也没写</span>
					</div>
				</div>
				<div class="user-fans">
					<div>
						<p>{{fans}}</p>
						<span>粉丝数</span>
					</div>
					<div>
						<p>{{followNum}}</p>
						<span>关 注</span>
					</div>
					<div>
						<span v-if="isFollow" @click="handleNotFollow()">已关注</span>
						<el-button type="primary" v-if="!isFollow" @click="handleFollow()">关注</el-button>
					</div>
				</div>
			</div>
		</div>
		
		<!-- 菜单导航 -->
		<el-menu :default-active="'1'" mode="horizontal" @select="handleSelect">
			<el-menu-item index="1">关注的人</el-menu-item>
			<el-menu-item index="2">他的粉丝</el-menu-item>
			<el-menu-item index="3">他的帖子</el-menu-item>
		</el-menu>
		
		<!-- 关注的人 -->
		<div class="follow-container" v-if="selectIndex == 1">
			<div class="follow-item" v-for="item in followUserList" @click="routeToOthersPage(item)">
				<div class="follow-content">
					<div>
						<el-avatar :size="75" :src="item.headSculpture"/>
					</div>
					<div class="follow-info">
						<p>{{item.username}}</p>
						<span v-if="item.personalizedSignature">简介: {{item.personalizedSignature}}</span>
						<span v-else>简介: 这个人什么也没写</span>
						<span>最近更新：{{item.updateTime}}</span>	
					</div>
				</div>
				<el-button type="primary" @click.stop="handleFollow(item.userId)">关注</el-button>
				
			</div>
			<div class="no-follow">
				<span v-if="followInfoPage.total == 0" >他还没有关注的人</span>
			</div>
			<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="followInfoPage.pageNum" :page-size="followInfoPage.pageSize" :total="followInfoPage.total"
						layout="prev, pager, next" @current-change="handleFollowCurrentChange">
				</el-pagination>
			</div>
		</div>
		
		<!-- 他的粉丝 -->
		<div class="follow-container" v-if="selectIndex == 2">
			<div class="follow-item" v-for="item in fansList" @click="routeToOthersPage(item)">
				<div class="follow-content">
					<div>
						<el-avatar :size="75" :src="item.headSculpture"/>
					</div>
					<div class="follow-info">
						<p>{{item.username}}</p>
						<span v-if="item.personalizedSignature">简介: {{item.personalizedSignature}}</span>
						<span v-else>简介: 这个人什么也没写</span>
						<span>最近更新：{{item.updateTime}}</span>	
					</div>
				</div>
				<el-button type="primary" @click.stop="handleFollow(item.userId)">关注</el-button>
			</div>
			<div class="no-follow">
				<span v-if="fansInfoPage.total == 0" >他还没有粉丝</span>
			</div>
			<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="followInfoPage.pageNum" :page-size="followInfoPage.pageSize" :total="followInfoPage.total"
						layout="prev, pager, next" @current-change="handleFollowCurrentChange">
				</el-pagination>
			</div>
		</div>	
		
		<!-- 他的帖子 -->
		<div v-if="selectIndex == 3">
			<div class="others-post-item" v-for="item in postList" @click="routeToPostDetail(item.postId)">
				<div>
					<el-avatar :size="60" :src="item.headSculpture" @click="routeToOthersPage(item)"/>
				</div>
				<div class="others-post-content">
					<div>
						<p>{{item.title}}</p>
						<img v-if="item.commentNum > 20" src="../../assets/icon/hot.gif"/>
						<img v-if="item.isEssences == 1" src="../../assets/icon/digest.gif"/>
						<span>最近更新：{{item.updateTime}}</span>
						<span>内容：{{item.content}}</span>
					</div>
				</div>
			</div>
			<div class="no-follow">
				<span v-if="queryInfo.total == 0" >他还没有帖子</span>
			</div>
			<!-- 分页导航 -->
			<div class="follow-pagination">
				<el-pagination :current-page.sync="followInfoPage.pageNum" :page-size="followInfoPage.pageSize" :total="followInfoPage.total"
						layout="prev, pager, next" @current-change="handleFollowCurrentChange">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import { getFollowList,getFollowNum,getFans,getFansList,follow,notFollow,getPostListByUserId } from '../../api/user'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default{
		name: 'othersInfo',
		inject: ['reload'],
		data(){
			return {
				queryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				followInfoPage: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				fansInfoPage: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				postNum: 0,
				fans: 0,
				followNum: 0,
				//选中的菜单项
				selectIndex: 1,
				isFollow: false,
				//用户信息
				user: {},
				fansList:[],
				postList: [],
				followUserList:[]
			}
		},
		methods:{
			/* 获取用户信息 */
			getUserInfo(){
				this.user = JSON.parse(window.sessionStorage.getItem('othersInfo'));
			},
			
			
			/* 获取关注列表 */
			handleGetFollowList(){
				const data = {
					subscribe: this.user.userId,
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getFollowList(data).then(res => {
					if(responseStatus(res)){
						this.followInfoPage = res.data.data;
						this.followUserList = res.data.data.list; 
					}
				})
			},
			
			/* 获取粉丝列表 */
			handleGetFans(){
				const data = {
					beSubscribe: this.user.userId,
					pageNum: this.fansInfoPage.pageNum,
					pageSize: this.fansInfoPage.pageSize
				}
				getFansList(data).then(res => {
					this.fansInfoPage = res.data.data;
					this.fansList = res.data.data.list;
				})
			},
			
			
			/* 获取他的帖子列表 */
			getPostList(){
				const data = {
					userId: this.user.userId,
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getPostListByUserId(data).then(res => {
					if(responseStatus(res)){
						this.queryInfo = res.data.data;
						this.postList = res.data.data.list; 
					}  	
				})
			},
			
			/* 获取发表的帖子数 */
			getPostNum(){
				this.$axios.get('/getPostNum', {
					params: { userId: this.user.userId }})
						   .then(res => {	
								this.postNum = res.data.data;  	
							})
			},
			
			
			/* 获得关注数 */
			handleGetFollowNum(){
				const data = {
					userId: this.user.userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/* 获取粉丝数 */
			handleGetFansList(){
				const data = {
					userId: this.user.userId
				}
				getFans(data).then(res => {
					if(responseStatus(res))
						this.fans = res.data.data; 
				})
			},
			
			/**
			 * 检测是否关注
			 */
			justifyFollow(){
				this.$axios.get('/user/checkSubscribe',{
					params: { userId: this.user.userId }})
						   .then(res => {
							   if(res.status !== 200)
							   	return this.$message.error("获取用户失败!!!");
							   else if(res.data.status !== 10000)
							   	this.$message.error(res.data.msg+"!!!");
							   else{
								   this.isFollow = res.data.data; 
								   console.log(this.isFollow);
							   }
							   	
						   })
			},
			
			/* 取消关注 */
			handleNotFollow() {
				const data = {
					beSubscribe: this.user.userId
				}
				notFollow(data).then((res) => {
					if(responseStatus(res))
						this.justifyFollow();
						return this.$message.success("取消成功!");	   							   
				});
			},
			
			/* 关注用户 */
			handleFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				if(data.beSubscribe == null){
					data.beSubscribe = this.user.userId
				}
				follow(data).then(res => {
					if(responseStatus(res)){
						this.justifyFollow();
						return this.$message.success("关注成功!");
					}
				});
			},
			
			/* 当前页数改变  */
			handleFollowCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.handleGetFollowList();
			},
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.getPostList();
			},
			
			/* 菜单选择器 */
			handleSelect(index){
				this.selectIndex = index;
			},
			
			/* 跳转到帖子详情 */
			routeToPostDetail(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(item){
				let othersInfo = JSON.stringify(item);
				window.sessionStorage.setItem('othersInfo',othersInfo);
				this.reload();
			}
		},
		created() {
			//获取他的关注列表
			this.getUserInfo();
			this.handleGetFollowNum();
			this.handleGetFans();
			this.getPostNum();
			this.handleGetFollowList();
			this.justifyFollow();
			this.handleGetFansList();
			this.getPostList();	
		}
	}
</script>

<style scoped="scoped">
	/**
	 * 个人信息
	 */
	.myself-container {
		color: #282C34;
		background-color: #FFFFFF;
	}
	.userInfo-background{
		width: 100%;
		height: 300px;
		position: relative;
		background: url(../../assets/background/background_content.jpg) no-repeat;
		background-size:100% 100%;
		border-radius: 10px;
	}
	.box {
		padding-top: 210px;
	}
	.user-detail-item{
		display: flex;
		max-width: 1200px;
		padding: 0 20px;
		justify-content: space-between;
	}
	.user-info{
		display: flex;
		align-items: center;
	}
	.user-info div:nth-child(2){
		margin-left: 10px;
	}
	.user-info div:nth-child(2) span{
		display: block;
		font-size: 14px;
		margin-top: 10px;
	}
	.user-fans{
		font-size: 14px;
		display: flex;
		align-items: center;	
	}
	.user-fans div{
		text-align: center;
		margin-left: 20px;
	}
	.user-fans div p{
		margin-bottom: 10px;
	}
	
	/**
	 * 我的关注
	 */
	.follow-container {
		background-color: #F8F8F8;
	}
	.follow-menu {
		padding: 0 20px;
	}
	.follow-item {
		display: flex;
		align-items: center;
		padding: 20px;
		cursor: pointer;
		justify-content: space-between;
		border-bottom: 1px #E8E8E8 solid;
	}
	.follow-item p:hover {
		color: #409EFF;
	}
	.follow-content{
		display: flex;
	}
	.follow-info {
		padding-left: 15px;
	}
	.follow-info p{
		margin: 5px 0;
		font-size: 18px;
	}
	.follow-info span{
		display: block;
		color: #666666;
		margin-top: 10px;
		font-size: 14px;
	}
	.follow-pagination {
		padding-top: 10px;
		text-align: center;
		background-color: #FFFFFF;
	}
	.no-follow {
		text-align: center;
		padding: 20px 0;
		background-color: #FFFFFF;
	}
	.no-follow span {
		color: #808080;
		font-size: 14px;
	}
	
	
	/**
	 * 他的帖子
	 */
	.others-post-item {
		display: flex;
		align-items: center;
		padding: 20px;
		cursor: pointer;
		border-bottom: 1px #EAEDF1 solid;
	}
	
	.others-post-content{
		margin-left: 10px;
	}
	.others-post-content p {
		max-width: 60vw;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		display: inline-block;
	}
	.others-post-content img {
		margin: 0 5px;
	}
	.others-post-content span {
		max-width: 85vw;
		display: block;
		font-size: 12px;
		margin-top: 10px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	.others-post-content p:hover {
		color: #409EFF;
	}
	
	.post-list-pagination {
		text-align: right;
	}
</style>
