<template>
	<div>
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" style="margin-bottom: 10px;">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>分类管理</el-breadcrumb-item>
		  <el-breadcrumb-item>分类列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card>
			<div slot="header">
			    <el-button  type="primary" @click="addDialogVisible=true">添加分类</el-button>
			</div>
			<div>
				<!-- 分类列表 -->
				<el-table :data="categoryList" style="width: 100%">
					<el-table-column prop="categoryId" label="分类ID"/>
					<el-table-column prop="categoryImage" label="分类图片">
						<template slot-scope="scope">
						　　　　<img :src="scope.row.categoryImage" width="50" height="50"/>
						</template>
					</el-table-column>
					<el-table-column prop="categoryName" label="分类名字"/>
					<!-- <el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="修改" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-edit" @click="showUpdateDialog(scope.row.categoryId)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
								<el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteDialogVisible=true"></el-button>
							</el-tooltip>
						</template>
					</el-table-column> -->
				</el-table>
			</div>
			<!-- 分页区域 -->
			<el-pagination layout="prev, pager, next" :total="queryInfo.pageTotal" />
		</el-card>
		
		<!-- 添加分类对话框 -->
		<el-dialog title="添加分类" :visible.sync="addDialogVisible" width="40%" @close="addDialogClosed">
			<!-- 添加分类表单 -->
			<el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px">
					<el-form-item label="分类名称" prop="categoryName">
						<el-input v-model="addForm.categoryName"></el-input>
					</el-form-item>
					<el-form-item>
						<el-upload action="*" ref="upload" list-type="picture-card"
							:auto-upload="false"
							:http-request="handleAddCategory"
							:limit="1">
							<img v-if="imageUrl" :src="imageUrl">
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
				</el-form>
			<!-- 底部按钮 -->
			<span slot="footer">
				<el-button @click="addDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="upload">增 加</el-button>
			</span>
		</el-dialog>
		
		<!-- 修改分类对话框 -->
		<el-dialog title="修改分类" :visible.sync="updateDialogVisible" width="40%" @close="updateDialogClosed">
			<!-- 修改分类表单 -->
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="分类ID" prop="categoryId">
						<el-input v-model="updateForm.categoryId"></el-input>
					</el-form-item>
					<el-form-item label="分类图片" prop="categoryImage">
						<el-input v-model="updateForm.categoryImage"></el-input>
					</el-form-item>
					<el-form-item label="分类名称" prop="categoryName">
						<el-input v-model="updateForm.categoryName"></el-input>
					</el-form-item>
				</el-form>
			<!-- 底部按钮 -->
			<span slot="footer">
				<el-button @click="updateDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="updateCategory">修改</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { addCategory,getCategory } from '../../api/admin'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'categoryList',
		data(){
			return {
				imageUrl: '',
				//列表信息
				queryInfo:{
					//总页数
					pageTotal: 0
				},
				//添加分类对话框显示&隐藏
				addDialogVisible: false,
				//修改分类对话框显示&隐藏
				updateDialogVisible: false,
				//添加分类的表单数据
				addForm: {
					categoryName: ''
				},
				//修改分类的表单数据
				updateForm: {
					categoryId: '',
					categoryImage: '',
					categoryName: ''
				},
				//添加表单的验证规则
				addFormRules: {
					categoryName: [
						{ required:true, message:'分类名称不能为空', trigger:'blur'}
					]
				},
				//修改表单的验证规则
				updateFormRules: {
					categoryId: [
						{ required:true, message:'分类ID不能为空', trigger:'blur'}
					],
					categoryName: [
						{ required:true, message:'分类名称不能为空', trigger:'blur'}
					]
				},
				//分类列表信息
				categoryList: []
			}
		},
		methods:{
			//监听页码值改变事件
			handleCurrentChange(newPageNum){
				//console.log(newPage);
				this.queryInfo.pageNum = newPageNum;
				this.getCategory();
			},
			//监听添加分类对话框关闭事件
			addDialogClosed(){
				this.$refs.addFormRef.resetFields(); //清空输入框
			},
			//监听修改分类对话框开启事件
			showUpdateDialog(categoryId){
				this.updateDialogVisible = true;
				this.$axios.get('/searchCategory', {
					params: { categoryId: categoryId }
				})
						   .then(res => {
								if(res.status !== 200)
									this.$message.error("查询失败!!!");
							   	else if(res.data.status !== 10000)
							   		this.$message.error(res.data.msg+"!!!");
							   	else
							   		this.updateForm = res.data.data;
						   })
			},
			//监听修改分类对话框关闭事件
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //清空输入框
			},
			/**
			 * 添加分类
			 */
			handleAddCategory(file){
				this.$refs.addFormRef.validate(valid => {
					const categoryFormData = new FormData();
					categoryFormData.append('categoryName',this.addForm.categoryName);
					categoryFormData.append('file', file.file);
					if(valid){
						addCategory(categoryFormData).then(res => {
							if(responseStatus(res)){
								this.$message.success("创建成功!!!");
								this.addDialogVisible = false;
								this.handleGetCategory();
							}
						})
					}
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			//修改分类
			updateCategory(){
				this.$refs.updateFormRef.validate(valid =>{
					if(valid){
						this.$axios.post('admin/updateCategory',this.$qs.stringify({ 
							categoryId: this.updateForm.categoryId,
							categoryImage: this.updateForm.categoryImage,
							categoryName: this.updateForm.categoryName}))
								   .then(res => {
									   //console.log(res);
									   if(res.status !== 200)
											this.$message.error("修改分类失败!!!");
										else if(res.data.status !== 10000)
											this.$message.error(res.data.msg+"!!!");
										else
											this.$message.success("修改成功!!!");
								   })
					}else return;
				})
			},
			/**
			 * 获得分类列表
			 */
			handleGetCategory(){
				getCategory().then(res => {
					if(responseStatus(res)){
						this.categoryList = res.data.data;
					}
				})
			}
		},
		created() {
			this.handleGetCategory();
		}
	}
</script>

