<template>
	<el-container>
		<el-header>
			<!-- 标题 -->
			<div class="title">
				<h1>校 园 论 坛</h1>
				<avatar-component :userInfo="user" :messageNum="messageNum" class="hidden-sm-and-up"></avatar-component>
			</div>
			<!-- 导航菜单 -->
			<el-menu :default-active="activeIndex" active-text-color="#409EFF" mode="horizontal" router>
				<el-menu-item index="/homePage">首页</el-menu-item>
				<el-submenu index="">
					<template slot="title">帖子</template>
					<el-menu-item index="/postListPage">帖子列表</el-menu-item>
					<el-menu-item index="/postingPage">发表帖子</el-menu-item>
					<el-menu-item index="/postManagePage">帖子管理</el-menu-item>
				</el-submenu>
				<el-menu-item index="/setting">设置</el-menu-item>
				<!-- 搜索框 -->
				<el-menu-item id="search-input">
					<el-input ref="searchRef" placeholder="请输入搜索关键词" v-model="searchKeyword" suffix-icon="el-icon-search" @focus="focusSearchInput"></el-input>
						<ul class="search-list" v-if="showSearchList" v-infinite-scroll="loadSearchResult" @mouseleave="blurSearchInput">
							<li class="search-item" v-for="item in searchResultList" @click="routeToPostDetail(item.postId)">
								<p>{{item.title}}</p>
								<div>
									<span style="color: #FFA500;">{{item.categoryName}}</span>
									<span style="color: #808080;">{{item.content}}</span>
								</div>
							</li>
							<div class="search-list-bottom">
								<span v-if="loading">加载中...</span>
								<span v-if="noMore">没有更多了</span>
							</div>
						</ul>
				</el-menu-item>
				<el-menu-item class="hidden-xs-only" style="float: right;">
					<avatar-component :userInfo="user" :messageNum="messageNum"></avatar-component>
				</el-menu-item>
			</el-menu>
		</el-header>
		<el-main>
			<router-view></router-view>
		</el-main>
	</el-container>
</template>

<script>
import avatarComponent from '../../components/users/user_component/avatarComponent.vue'
import { getSearchList,getUser,getMessageNum } from '../../api/user'
import { responseStatus } from '../../utils/responseStatusUtil'
export default {
	name: 'home',
	inject: ['reload'],
	components:{
		avatarComponent: avatarComponent
	},
	data() {
		return {
			queryInfo: {
				pageNum: 1,
				pageSize: 5,
				total: 0
			},
			user: {},
			searchResultList: [],
		    activeIndex: '/homePage',
			searchKeyword: ''	,//搜索关键字
			showSearchList: false,
			loading: false,
			messageNum: 0,
			messageTimer: null
		  }
	},
	computed:{
		noMore: function(){
			return this.queryInfo.isLastPage;
		}
	},
	methods:{
		
		/**
		 * 获取新消息
		 */
		handleGetMessageNum(){
			getMessageNum().then(res =>{
				if(responseStatus(res))
					this.messageNum = res.data.data;
			})	
		},
		
		handleMessageTimer(){
			this.$timerCount[0] = setInterval(() => {
				this.handleGetMessageNum();
			},3000)
		
		},
		
		/**
		 * 从网络获取用户信息
		 */
		handleGetUser(){
			getUser().then(res => {
				if(responseStatus(res)){
					this.user = res.data.data; 	
				}
			})
		},
		
		/**
		 * 搜索下拉框的显示与隐藏
		 */
		focusSearchInput(){
			this.showSearchList = true;
		},
		blurSearchInput(){
			this.$refs.searchRef.blur();
			this.showSearchList = false;	
		},
		/**
		 * 获得搜索结果列表
		 * @param {Object} searchKeyword 搜索关键字
		 */
		handleSearch(){
			const data = {
				content: this.searchKeyword,
				pageNum: this.queryInfo.pageNum,
				pageSize: this.queryInfo.pageSize,
			}
			if(this.searchKeyword != ''){
				getSearchList(data).then(res => {
					if(responseStatus(res)){
						this.searchResultList = this.searchResultList.concat(res.data.data.list);
						this.queryInfo = res.data.data;
						
					}
				})
			}
			
		},
		
		/**
		 * 搜索结果加载
		 */
		loadSearchResult() {
			if(this.queryInfo.hasNextPage){
				this.loading = true;
				this.queryInfo.pageNum += 1;
				this.handleSearch();
				this.loading = false;
			}
		},
		
		/*页面跳转*/
		routeToPostDetail(postId){
			this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			this.reload();
		},
	
	},
	watch:{
		searchKeyword: function(){
			this.searchResultList = [];
			this.queryInfo.pageNum = 1;
			this.handleSearch();
		}
	},
	mounted() {
		 this.$bus.$on('change', ()=> {
		    this.reload();
		  })
	},
	created() {
		this.handleGetUser();
		this.handleMessageTimer();
	}
}
</script>

<style scoped>
	
	.el-header {
		height: max-content !important;
	}
	.title {
		display: flex;
		padding-top: 10px;
		justify-content: space-between;
	}
	.title h1 {
		margin: 0;
		color: #409EFF;
		font-size: 26px;
	}
	
	/* 搜索框 */
	.search-list {
		max-height: 250px;
		overflow-y: scroll;
		background-color: #FFF;
		box-shadow: 0 15px 30px 0 rgba(0, 0, 1, .1);
		position: relative;
		z-index: 100000;
	}
	
	.search-item {
		display: flex;
		padding: 0 20px;
		align-items: center;
		justify-content: space-between;
	}
	.search-item p {
		max-width: 200px; 
		overflow-x: hidden;
	}
	.search-item span{
		max-width: 120px;
		line-height: 20px;
		font-size: 12px;
		overflow: hidden;
		text-align: right;
		display: block;
	}
	.search-item:hover {
		background-color: #F8F8F8;
	}
	.search-list-bottom {
		color: #BBBBBB;
		text-align: center;
	}
	
	

	/**
	 * 媒体查询
	 */
	@media only screen and (max-width: 768px) {
		#search-input {
			width: 100%;
		}
	}
	@media only screen and (min-width: 780px) {
		#search-input {
			width: 350px;
		}
	}
	
	
</style>
