<template>
	<div>
		<!-- xs尺寸用户信息 -->
		<div class="detail-item-user-mobile hidden-sm-and-up">
			<header class="item-user-mobile">
				<div>
					<div @click="routeToOthersPage(comment)">
						<el-avatar :size="50" :src="comment.headSculpture"/>
					</div>
					<div class="item-user-info-mobile">
						<p :style="usernameColor">{{comment.username}}</p>
						<span @click="routeToOthersPage(comment)">粉丝：{{fansNum}}</span>
						<span @click="routeToOthersPage(comment)">关注：{{followNum}}</span>
						<span @click="routeToOthersPage(comment)">帖子数：{{postNum}}</span>
					</div>
				</div>
				<p style="font-size: 12px;">{{index+1}}楼</p>
			</header>
		</div>
		<el-row type="flex" id="comment-container">
				<!-- 用户信息 -->
				<el-col class="comment-item-user hidden-xs-only" :xs="24" :sm="5" :md="5" :lg="4" :xl="4">
					<div class="item-user-info">
						<div @click="routeToOthersPage(comment)">
							<el-avatar :size="100" :src="comment.headSculpture" style="cursor: pointer;"></el-avatar>
							<p :style="usernameColor">{{comment.username}}</p>
						</div>
						<table style="width: 100%;">
							<tr>
								<th @click="routeToOthersPage(comment)">
									<p>{{followNum}}</p>
									<span>关 注</span>
								</th>
								<th @click="routeToOthersPage(comment)">
									<p>{{fansNum}}</p>
									<span>粉丝数</span>
								</th>
								<th @click="routeToOthersPage(comment)">
									<p>{{postNum}}</p>
									<span>帖 子</span>
								</th>
							</tr>
						</table>
					</div>	
				</el-col>
				<el-col :xs="24" :sm="19" :md="19" :lg="20" :xl="20">
					<div class="comment-item-detail">
						<!-- 内容头部 -->
						<div class="comment-item-header">
							<div>
								<span class="hidden-xs-only" style="font-size: 12px;">{{index+1}}楼</span>
							</div>
							<span style="color: #888;">
								<i class="el-icon-date"></i>
								&nbsp;发表于&nbsp; {{comment.commentTime}}
							</span>
						</div>
						<div>
							<mavon-editor :value="comment.content" defaultOpen="preview" :boxShadow="false" :editable="false"
								:subfield="false" :toolbarsFlag="false" previewBackground="#FFF">
							</mavon-editor>
						</div>
						<div v-if="!isMTE(comment.content)">
							<img v-for="item in images" :src="item" width="100%" height="100%"/>
						</div>
						<div class="comment-item-bottom">
							<el-dropdown size="mini" @command="handleCommand" v-if="isPostUser||isCommentUser">
							  <span style="font-size: 12px; color: #666666;">
							    管理操作<i class="el-icon-arrow-down el-icon--right"></i>
							  </span>
							  <el-dropdown-menu slot="dropdown">
							    <el-dropdown-item command="delete">删除评论</el-dropdown-item>
							  </el-dropdown-menu>
							</el-dropdown>
							<span style="color: #BBB; margin: 0 5px;">丨</span>
							<span>举报</span>
						</div>
					</div>
				</el-col>
		</el-row>
	</div>
	
</template>

<script>
	import {deleteMyComment, getPostImage,getFollowNum,getFans,getPostNum } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	import { textColorRandom } from '@/utils/colorUtils'
	export default {
		name: 'postCommentComponent',
		props: { 
			comment: {type: Object, requires: true},
			index: {type: Number, requires: true},
			isPostUser: {type: Boolean, requires: true}
		},
		data() {
			return {
				followNum: 0,	//关注数
				fansNum: 0,		//粉丝数
				postNum: 0,		//帖子数
				images: [],
				user: {}	//登录的用户
			}
		},
		computed: {
			/* 用户名颜色 */
			usernameColor: function(){
				return {
					color: textColorRandom()
				}
			},
			isCommentUser: function(){
			 	return this.comment.userId == this.user.userId;
			}
		},
		methods:{
			
			/* 获得关注数 */
			handleGetFollowNum(){
				const data = {
					userId: this.comment.userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/* 获取粉丝数 */
			handleGetFansNum(){
				const data = {
					userId: this.comment.userId
				}
				getFans(data).then(res => {
					if(responseStatus(res))
						this.fansNum = res.data.data; 
				})
			},
			
			/* 获取发表的帖子数 */
			handleGetPostNum(){
				const data = {
						userId: this.comment.userId
					}
				getPostNum(data).then(res => {
					if(responseStatus(res))
						this.postNum = res.data.data; 
				})
			},
			
			
			
			/* 获取图片 */
			handleGetPostImage(){
				const data = {
					postId: this.post.postId
				}
				getPostImage(data).then(res => {
					if(responseStatus(res))
						this.images = res.data.data;
				})
			},
			
			/* 删除评论 */
			handleDeleteComment(commentId) {
				const data = {
					commentId: commentId
				}
				deleteMyComment(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.$emit('freshCommentList');
					}				   							   
				})
			},
			
			isMTE(){
				if(this.comment.content == null) return false
				if(this.comment.content.search(/\[[\s\S]*?\]\([\s\S]*?\)/) != -1){
					return true;
				}else{
					return false;
				}
			},
			/**
			 * 下拉菜单处理事件
			 */
			handleCommand(command){
				if(command == 'delete'){
					//删除评论
					this.handleDeleteComment(this.comment.commentId);
				}
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(item){
				window.sessionStorage.setItem('othersInfo',JSON.stringify(item));
				this.$router.push({path: '/othersInfo'});
			},
			
		},
	 	created() {
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
			this.handleGetFollowNum();
			this.handleGetPostNum();
			this.handleGetFansNum();		
		}
	}
</script>

<style lang="less" scoped>
	
	#comment-container {
		border-bottom: 1px #DDDDDD solid;
		
		/* 用户信息样式 */
		.comment-item-user {
			padding: 20px;
			font-size: 14px;
			text-align: center;
			background-color: #F8F8F8;
			
			//用户名
			.item-user-info p:nth-child(2) {
				font-weight: 600;
				margin: 10px;
			}
		}
		
		/* 评论内容 */
		.comment-item-detail {
			padding: 20px;
			padding-bottom: 0px;
			
			.comment-item-header {
				margin-bottom: 10px;
				font-size: 12px;
				
				div {
					font-size: 14px;
					text-align: right;
					margin-bottom: 10px;
				}
			}
			
			.comment-item-bottom {
				color: #666666;
				font-size: 12px;
				text-align: right;
				margin: 5px 0;
				
				span:hover {
					color: #409EFF;
					cursor: pointer;
				}
			}
		}
	}
	
	/**
	 * xs样式
	 */
	
	/* 用户信息样式 */
	.detail-item-user-mobile {
		padding: 10px;
		font-size: 14px;
		background-color: #F8F8F8;
		
		.item-user-mobile {
			display: flex;
			justify-content: space-between;
		}
		
		.item-user-mobile > div {
			display: flex;
			align-items: center;
			//用户信息
			.item-user-info-mobile {
				margin-left: 10px;
				p {
					font-size: 14px;
					font-weight: 600;
				}
				span {
					font-size: 12px;
					color: #888888;
					display: inline-block;
					margin-right: 10px;
					margin-top: 5px;
				}
				span:hover {
					color: #409EFF;
					cursor: pointer;
					text-decoration: underline;
				}
			}
		}
	}
	
	
</style>
