<template>
			<el-row type="flex" class="comment-list-item">
				<!-- 用户信息 -->
				<el-col :span="3" class="comment-user-info hidden-xs-only">
					<el-avatar :size="70" :src="comment.headSculpture"></el-avatar>
				</el-col>
				<el-col :xs="24" :sm="21" :md="21" :lg="21" :xl="21" class="comment-list-item-content">
					<!-- 内容头部 -->
					<header>
						<div>
							<h3 @click="routeToPostDetail(comment.parentId?comment.parentId:comment.postId)">原帖：{{comment.title}}</h3>
							<div class="comment-list-item-content-date">
								<span>发表于 {{comment.commentTime?comment.commentTime:comment.createTime}}</span>
								<span v-if="!comment.commentId" style="margin-left: 10px;">评论数：{{comment.commentNum}}</span>
								<span v-if="comment.commentId" @click="handleDeleteComment(comment.commentId)">删除评论</span>
								<span v-if="!comment.commentId" @click="handleDeletePost(comment.postId)">删除帖子</span>
							</div>
						</div>
						<span v-if="comment.categoryName">【{{comment.categoryName}}】</span>
					</header>
					
					<mavon-editor :value="comment.content" defaultOpen="preview" :boxShadow="false" :editable="false"
						:subfield="false" :toolbarsFlag="false" previewBackground="#FFF" style="min-width: 100px;">
					</mavon-editor>
					
				</el-col>
			</el-row>
	
		
</template>

<script>
	import { deleteMyComment,deleteMyPost,getPostById } from '../../../api/user'
	import { responseStatus } from '../../../utils/responseStatusUtil'
	export default {
		name: 'commentListComponent',
		props: { comment: {type: Object, requires: true}},
		data() {
			return {
				user: {}
			}
		},
		methods:{
			/**
			 * 删除评论
			 * @param {Object} commentId
			 */
			handleDeleteComment(commentId) {
				const data = {
					commentId: commentId
				}
				deleteMyComment(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.$parent.handleGetMyCommentList();
					}				   							   
				})
			},
			/**
			 * 删除帖子
			 * @param {Object} commentId
			 */
			handleDeletePost(postId) {
				const data = [];
				data.push(parseInt(postId));
				deleteMyPost(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.$parent.handleGetMyPostList();
					}				   							   
				})
			},
			
			
			
			/* 跳转到帖子详情 */
			routeToPostDetail(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
		},
		created() {
		}
	}
</script>

<style scoped="scoped">
	/**
	 * 我的评论
	 */
	.comment-user-info {
		padding: 20px;
		text-align: center;
		vertical-align: middle;
		background-color: #F8F8F8;
	}
	.comment-list-item {
		font-size: 14px;
		border-bottom: 1px #CCCCCC solid;
	}
	.comment-list-item-content {
		padding: 10px;
	}
	
	/* 我的评论列表内容头部 */
	.comment-list-item-content header{
		display: flex;
		justify-content: space-between;
	}
	.comment-list-item-content header h3{
		color: #409EFF;
		font-weight: normal;
		display: inline-block;
		margin: 10px 5px;
		cursor: pointer;
	}
	.comment-list-item-content span:last-child{
		color: #FFA500;
	}
	.comment-list-item-content-date {
		font-size: 12px;
		margin: 5px;
		border-bottom: 1px #EAEDF1 dashed; 
	}
	.comment-list-item-content-date span:first-child {
		color: #666666;
	}
	.comment-list-item-content-date span:last-child {
		color: #FF4000; 
		cursor: pointer;
		margin-left: 20px; 
	}
</style>
