import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '../views/login.vue'
import register from '../views/register.vue'
import home from '../views/user/home.vue'



/**
 * 前台
 */
import forgetPassword from '../views/forgetPassword.vue'
import homePage from '../components/users/home/homePage.vue'
import postDetailPage from '../components/users/postDetailPage.vue'
import postListPage from '../components/users/post_list/postListPage.vue'
import searchResultPage from '../components/users/searchResultPage.vue'
import othersInfo from '../components/users/othersInfo.vue'
import postingPage from '../components/users/postingPage.vue'
import messagePage from '../components/users/messagePage.vue'
import subscribeCategoryPostListPage from '../components/users/home/subscribeCategoryPostListPage.vue'
import setting from '../components/users/setting.vue'
import userCenter from '../components/users/userCenter.vue'
import postManagePage from '../components/users/post_manage/postManagePage.vue'
/**
 * 后台
 */
import adminHome from '../views/admin/adminHome.vue'
import adminUserList from '../components/admin/adminUserList.vue'
import trashStation from '../components/admin/trashStation.vue'
import adminPostList from '../components/admin/adminPostList.vue'
import banUser  from '../components/admin/banUser.vue'
import categoryList from '../components/admin/categoryList.vue'
//安装插件
Vue.use(VueRouter)

//配置路由信息
const routes = [
	{ path: '/', redirect: '/login' },
	{ path:'/login', component: login },
	{ path:'/register', component: register },
	{ path:'/forgetPassword', component: forgetPassword },
	 { path: '/home', redirect: '/homePage' },
	{ path: '/adminHome', redirect: '/adminUserList' },
	{
		path:'/adminHome',
		component: adminHome,
		children: [{ path: '/banUser', component: banUser},
				   { path: '/adminUserList', component: adminUserList},
				   { path: '/categoryList', component: categoryList},
				   { path: '/trashStation', component: trashStation},
				   { path: '/adminPostList', component: adminPostList}]
	},
	{
		path:'/home',
		component: home,
		children: [
				   { path: '/homePage', component: homePage},
				   { path: '/setting', component: setting},
				   { path:'/userCenter', component: userCenter },
				   { path:'/postDetailPage', component: postDetailPage },
				   { path:'/postListPage', component: postListPage },
				   { path:'/messagePage', component: messagePage },
				   { path:'/othersInfo', component: othersInfo },
				   { path:'/postingPage', component: postingPage },
				   { path:'/postManagePage', component: postManagePage },
				   { path:'/subscribeCategoryPostListPage', component: subscribeCategoryPostListPage }]
	}
]

const router = new VueRouter({
	routes: routes
})

const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error=> error)
}

//挂载路由导航守卫
router.beforeEach((to, from, next) => {
	if(to.path === '/login' || to.path === '/register' || to.path === '/forgetPassword') 
		return next();
	//获取token
	const accessToken = window.localStorage.getItem('accessToken');
	if(!accessToken) 
		return next('/login');
	next();
})

export default router