<template>
	<div>
		<img src="~@/assets/home-images/background_content.jpg" style="width: 100%; max-height: 450px;" />
		<!-- 分页导航 -->
		<div class="subscribe-container">
			<el-button id="posting-button" @click="routeToPostingPage">发新帖</el-button>
			<el-pagination class="hidden-xs-only" :current-page.sync="pageInfo.pageNum" :page-size="pageInfo.pageSize" :total="pageInfo.total"
				layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</div>
		
		<!-- 关注的分类 -->
		<subscribeCategoryComponent></subscribeCategoryComponent>
		
		<!-- 关注的人的帖子 -->
		<subscribePostComponent :queryInfo="pageInfo" :postList="mySubscribePostList" :title="'关注的帖子'"></subscribePostComponent>
		
		<!-- 分页导航 -->
		<div class="post-list-pagination">
			<el-pagination :current-page.sync="pageInfo.pageNum" :page-size="pageInfo.pageSize" :total="pageInfo.total"
					layout="prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</div>			
	</div>
	
</template>

<script>
	import subscribePostComponent from './home_components/subscribePostComponent.vue'
	import subscribleCategoryComponent from './home_components/subscribeCategoryComponent.vue'
	import { getSubscribePostList } from '../../../api/user.js'
	import { responseStatus } from '../../../utils/responseStatusUtil'
	export default {
		name: 'homePage',
		components: {
			subscribeCategoryComponent: subscribleCategoryComponent,
			subscribePostComponent: subscribePostComponent
		},
		data(){
			return {
				//分页信息
				pageInfo: {
					pageNum: 1,		//当前页数
					pageSize: 10,	//分页大小
					total: 0		//总条数
				},
				//用户关注的用户的帖子
				mySubscribePostList: [],
				commentNum: [],
				isEdit: false,
			}
		},
		methods: {
			
			/**
			 * 获取用户关注的帖子列表
			 */
			handleSubscribePost(){
				const data = {
					pageNum: this.pageInfo.pageNum,
					pageSize: this.pageInfo.pageSize
				}
				getSubscribePostList(data).then(res => {
					if(responseStatus(res)){
						this.pageInfo = res.data.data;
						this.mySubscribePostList = res.data.data.list;
					}
				})
			},
			
			/* 分页处理 */
			handleCurrentChange(newPage){
				this.pageInfo.pageNum = newPage;
				this.handleSubscribePost();
			},
			
			/* 跳转到发表帖子页面 */
			routeToPostingPage(){
				this.$router.push('/postingPage');
			},
			
			
		},
		created() {
			this.handleSubscribePost();
		}
	}
</script>

<style scoped>
	.subscribe-container {
		margin: 10px 0;
		display: flex;
		justify-content: space-between;
	}
	
	#posting-button {
		color: #FFF;
		background-color: #68C7FF; 
		margin-left: 5px;
		border: none;
	}
	
	.post-list-pagination {
		margin: 10px 0;
		text-align: right;
		margin-right: 40px;
	}
</style>
