<template>
	<el-container class="admin-home-container">
		<!-- 头部 -->
		<el-header>
			<div class="title">论坛后台管理</div>
			<!-- 下拉菜单 -->
			<div class="user-item">
				<el-dropdown @command="handleCommand">
				    <el-avatar :size="40" :src="user.headSculpture"></el-avatar>
				    <el-dropdown-menu slot="dropdown">
						<el-dropdown-item icon="el-icon-circle-close" command="/home">前台界面</el-dropdown-item>
				        <el-dropdown-item icon="el-icon-circle-close" command="/logout" @click.native="handleLogout">退出登录</el-dropdown-item>
				    </el-dropdown-menu>
				</el-dropdown>
			</div>	
		</el-header>
		<!-- 页面主体 -->
		<el-container>
			<!-- 侧边栏 -->
			<el-aside :width="isCollapse? '64px' : '200px'">
				<!-- 菜单 -->
				<el-menu background-color="#282c34" text-color="#FFFFFF" active-text-color="#409EFF" 
						 unique-opened :collapse="isCollapse" :collapse-transition="false" router
						 :default-active="activePath">
					<!-- 侧边栏折叠展开 -->
					<el-menu-item class="toggle-button" @click="toggleCollapse">
						<i class="el-icon-s-fold"></i>
						<span slot="title">菜单</span>
					</el-menu-item>
					<el-submenu index="1">
					    <template slot="title">
					        <i class="el-icon-user"></i>
					        <span>用户管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[0]" @click="saveNavState(index[0])">
								<i class="el-icon-tickets"></i>
								<span>用户列表</span>
							</el-menu-item>
							<el-menu-item :index="index[1]" @click="saveNavState(index[1])">
								<i class="el-icon-lock"></i>
								<span>封禁账户</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="2">
					    <template slot="title">
					        <i class="el-icon-document"></i>
					        <span>文章管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[2]" @click="saveNavState(index[0])">
								<i class="el-icon-tickets"></i>
								<span>文章列表</span>
							</el-menu-item>
							<el-menu-item :index="index[3]" @click="saveNavState(index[0])">
								<i class="el-icon-delete"></i>
								<span>回收站</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="3">
					    <template slot="title">
					        <i class="el-icon-menu"></i>
					        <span>分类管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[4]" @click="saveNavState(index[0])">
								<i class="el-icon-tickets"></i>
								<span>分类列表</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>
			<!-- 右侧内容主体 -->
			<el-main>
				<router-view></router-view>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import { logout } from '@/api/login'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'adminHome',
		data(){
			return {
				user: {},
				//是否折叠
				isCollapse: true,
				//被激活的链接
				activePath: '/adminUserList',
				//路由路径
				index: [
					'/adminUserList',
					'/banUser',
					'/adminPostList',
					'/trashStation',
					'/categoryList'
				]
			}
		},
		created() {
			this.activePath = window.sessionStorage.getItem('activePath'),
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
		},
		methods: {
			//菜单折叠与展开
			toggleCollapse(){
				this.isCollapse = !this.isCollapse;
			},
			//保存链接的激活状态
			saveNavState(activePath){
				window.sessionStorage.setItem('activePath',activePath);
				this.activePath = activePath;
			},
			
			/**
			 * 下拉菜单处理事件
			 * @param {Object} command 菜单地址
			 */
			handleCommand(command){
				this.$router.push(command);
			},
			
			/* 退出登录 */
			handleLogout(){
				logout().then(res =>{
					if(responseStatus(res)){
						// 清空 token
						window.sessionStorage.clear();
						window.localStorage.clear();
						//跳转到登录页
						this.$router.push('login');
					}
				});	
			}
		}
	}
</script>

<style scoped="scoped">
	.admin-home-container {
		height: 100%;
		background-color: #EAEDF1;
	}
	/**
	 * 头部样式
	 */
	.el-header {
		display: flex;
		justify-content: space-between;
		background-color: #282c34;
	}
	/*头部左边样式*/
	.title{
		color: #FFFFFF;
		font-size: 25px;
		line-height: 60px;
		font-weight: bold;
	}
	/*头部右边样式*/
	.user-item {
		display: flex;
		align-items: center;
	}
	.log-out{
		color: #FFFFFF;
		margin-left: 10px;
		cursor: pointer;
	}
	.log-out:hover{
		text-decoration: underline;
	}
	/**
	 * 边栏样式
	 */
	.toggle-button {
		color: #FFFFFF !important;
	}
	.el-aside{
		background-color: #282c34;
	}
	.el-menu{
		border-right: none !important;
	}
	
</style>
